const example1 = () => {
  // 1. Create the scene
  const scene = new THREE.Scene()
  scene.background = new THREE.Color('#F0F0F0')

  // 2. Add the camera(can be later ...)
  const camera = new THREE.PerspectiveCamera(75, width/height, 0.1, 1000);
  camera.position.z = 5;

  // 3. Create and add a cube object by geometry, and material(mesh)
  const geometry = new THREE.IcosahedronGeometry()
  const material = new THREE.MeshLambertMaterial({
    color: '#dcc2e0',
    emissive:  '#BF40BF',
  })
  const odode = new THREE.Mesh(geometry, material)

  const geo_box = new THREE.BoxGeometry(2, 0.1, 2)
  const mat_box = new THREE.MeshStandardMaterial({
    color: '#c9ead8',
    emissive: '#5BC236',
    metalness: 0.5,
    roughness: 0.0,
  })

  const obox = new THREE.Mesh(geo_box, mat_box)
  obox.position.y = -1.5

  scene.add(odode)
  scene.add(obox)


  // 4. Add lighting
  //const light = new THREE.DirectionalLight(0x9CDBA6, 10)
  const light = new THREE.SpotLight(0x006769, 100)
  light.position.set(1,1,1)
  scene.add(light)


  const renderer = new THREE.WebGLRenderer({canvas})

  renderer.setSize(window.innerWidth, window.innerHeight)
  renderer.setPixelRatio(window.devicePixelRatio)

  //document.body.appendChild(renderer.domElement)

  // orbit
  const controls = new OrbitControls(camera, renderer.domElement)
  controls.enableDamping = true;
  controls.dampingFactor = 0.05
  controls.enableZoom = true;
  controls.enablePan = true


  // Add animation
  const animate = () => {
    requestAnimationFrame(animate)
    odode.rotation.x += 0.01
    odode.rotation.y += 0.02

    obox.rotation.y -= 0.005

    controls.update()

    renderer.render(scene, camera)
  }

  // make it resizable
  window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth/window.innerHeight
    camera.updateProjectionMatrix()
    renderer.setSize(window.innerWidth, window.innerHeight)
  })

  //==- NOTE: not so functional: scope used ------------------------------==//
  /*
  const render = () => {
    renderer.render(scene, camera)
  }
  // 6. Animate the scene
  const animate = () => {
    // refresh rate is 60hz
    requestAnimationFrame(animate)
    cube.rotation.x += 0.01
    cube.rotation.y += 0.01    
    render()
  }*/
  //==--------------------------------------------------------------------==//
  animate()
}


//skeleton()
example1()


